import React from 'react';

const Card = (props) => {
	return (
		<div className='tc bg-dark-blue dib br3 pa3 ma2 grow bw2 shadow-5'>
			<img alt='Football Players' src={props.link} />
				<div>
					<h2> {props.name} </h2>
					<p> {props.country} </p>
				</div>
		</div>
		);

}

export default Card;